.file-hidden {
    position: absolute;
    top: 0;
    font-size: 200px;
    right: 0;
    opacity: 0;
    filter: alpha(opacity=0);
    -moz-opacity: 0;
    cursor: pointer;
    display: block;
    direction: ltr;

}

.file-hidden-cover {
    position: relative;
    overflow: hidden;
}